import { topicRequestSuccess } from './../Actions';
import 'jest'
import { testSaga } from 'redux-saga-test-plan'
import { put, takeLatest } from 'redux-saga/effects';
import { getTopicList } from '../Sagas';
import HomeApi from '../../../Services/HomeService';


describe('getTopicList Saga', () => {
    let getTopicListGenerator: any;
    const params = { page: 1, tab: 'all', limit: 20, mdrender: 'true' };
    beforeEach((() => {
        getTopicListGenerator = getTopicList(params);

        const selectDescriptor = getTopicListGenerator.next(HomeApi().getTopicsRequest, params).value;
console.log('====================================')
console.log(selectDescriptor)
console.log('====================================')
        expect(selectDescriptor).toMatchSnapshot();

        // const callDescriptor = getTopicListGenerator.next().value;
        // expect(callDescriptor).toMatchSnapshot();
    }))

    it('should dispatch the topicRequestSuccess action if it requests the data successfully', () => {
        const response: never[] = [];
        const putDescriptor = getTopicListGenerator.next(response).value;
        console.log(putDescriptor);
        expect(putDescriptor).toEqual(put(topicRequestSuccess(response)));
      });
})